/* global clearInterval, console, setInterval */

/**
 * Add two numbers
 * @customfunction
 * @param {number} first First number
 * @param {number} second Second number
 * @returns {number} The sum of the two numbers.
 */
export function add(first, second) {
  return first + second;
}



/**
 * Writes a message to console.log().
 * @customfunction LOG
 * @param {string} message String to write.
 * @returns String to write.
 */
export function logMessage(message) {
  console.log(message);

  return message;
}

/**
 * get access token and parse it returning the some data
 * @customfunction GETTOKEN
 * @returns {string} access token data
 */
export async function getToken() {
  try {
    const token = await OfficeRuntime.auth.getAccessToken({ allowSignInPrompt: true });
    console.log("Token acquired: ", token);

    // Decode the JWT token to extract user information
    const base64Url = token.split(".")[1];
    const base64 = base64Url.replace(/-/g, "+").replace(/_/g, "/");
    const jsonPayload = decodeURIComponent(
      atob(base64)
        .split("")
        .map((c) => {
          return "%" + ("00" + c.charCodeAt(0).toString(16)).slice(-2);
        })
        .join("")
    );

    const payload = JSON.parse(jsonPayload);
    console.log("Token payload: ", payload);
    return `User: ${payload.name}, UPN: ${payload.upn}, OID: ${payload.oid}`;
  } catch (error) {
    console.error("Error acquiring token: ", error);
    return `Error: ${error.message}`;
  }
}
CustomFunctions.associate("ADD", add);
CustomFunctions.associate("LOG", logMessage);
CustomFunctions.associate("GETTOKEN", getToken);